.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-dark;
  border-radius: 50%;
  transition-property: color, background-color;
  transition-duration: 0.2s;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transition: width 0.2s, height 0.2s, transform 0.3s cubic-bezier(0.5, 0.18, 0.34, 1.6) 0.1s;
    transform: translate(-50%, -50%) scale(0);

    .page-loaded & {
      transform: translate(-50%, -50%) scale(1);
    }
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;

    &::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    &::before {
      border-color: $c-purple;
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }

  .social-block--story-page-1 & {
    color: $c-purple;

    &:hover {
      color: $c-dark;
      border-color: $c-dark;
    }

    &:focus {
      color: $c-dark;

      &::before {
        border-color: $c-dark;
      }
    }

    &:active {
      color: $c-light;

      &::before {
        background-color: $c-dark;
      }
    }
  }

  .social-block--story-page-2 & {
    color: $c-dark-blue;

    &:hover {
      color: $c-light-blue;
      border-color: $c-light-blue;
    }

    &:focus {
      color: $c-light-blue;

      &::before {
        border-color: $c-light-blue;
      }
    }

    &:active {
      color: $c-light;

      &::before {
        background-color: $c-light-blue;
      }
    }
  }

  .social-block--story-page-3 & {
    color: $c-light-blue;

    &:hover {
      color: $c-dark-blue;
      border-color: $c-dark-blue;
    }

    &:focus {
      color: $c-dark-blue;

      &::before {
        border-color: $c-dark-blue;
      }
    }

    &:active {
      color: $c-light;

      &::before {
        background-color: $c-dark-blue;
      }
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    li {
      opacity: 1;
      transform: translateY(-0.5rem);
    }

    li:nth-child(1) {
      transition-duration: 0.3s;
    }

    li:nth-child(2) {
      transition-duration: 0.6s;
    }

    li:nth-child(3) {
      transition-duration: 0.9s;
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;
    opacity: 0;
    transition-property: transform, opacity;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
      opacity: 1;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
      opacity: 1;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: all 0.25s;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    color: $c-purple;
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }

  .social-block--story-page-1 &:hover {
    color: $c-dark;
  }

  .social-block--story-page-2 &:hover {
    color: $c-light-blue;
  }

  .social-block--story-page-3 &:hover {
    color: $c-dark-blue;
  }
}
