.form {
  position: relative;
  display: flex;
  align-items: center;
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-bottom: 12px;
    font-size: 16px;
  }

  @media (orientation: portrait) {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field-wrapper {
  position: relative;
  flex: none;
  margin-right: 3rem;
  width: calc(100% - 21rem);
  height: 6rem;

  @media (min-width: $stop-scaling) {
    margin-right: 30px;
    width: calc(100% - 210px);
    height: 60px;
  }

  @media (orientation: portrait) {
    margin-right: 1rem;
    width: calc(100% - 5rem);
    height: 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-right: 1rem;
    width: calc(100% - 9rem);
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: calc(100% - 5rem);
    height: 4rem;
  }
}

.form__field {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 5.6rem;
  width: 100%;
  height: 100%;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-dark;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transform-origin: 100% 50%;
  animation: 0.2s ease-in 0s 1 normal both formFieldAppearance;

  @keyframes formFieldAppearance {
    0% {
      width: 0;
    }

    100% {
      width: 100%;
    }
  }

  &:focus {
    border: 1px solid $c-purple;
  }

  &:focus + .form__field-placeholder {
    z-index: -1;
  }

  @media (min-width: $stop-scaling) {
    padding: 0 56px;
    font-size: 24px;
    border-radius: 30px;
  }

  @media (orientation: portrait) {
    padding: 0 2.5rem;
    font-size: 1.6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 0 2.5rem;
    font-size: 1.6rem;
  }
}

.form__button {
  flex-grow: 1;

  @media (orientation: portrait) {
    position: relative;
    flex: none;
    padding: 0;
    width: 4rem;
    height: 4rem;
    font-size: 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    position: relative;
    flex: none;
    padding: 0;
    width: 6rem;
    height: 6rem;
    font-size: 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 4rem;
    height: 4rem;
  }

  svg {
    @include center;
    display: none;
    width: 1.6rem;
    height: 1.8rem;

    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 18px;
    }

    @media (orientation: portrait) {
      display: block;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      display: block;
    }
  }
}

.form--result {
  $buttonAnimationDelay: 1.1s;
  $buttonAnimationDuration: 0.2s;
  $inputAnimationDuration: 0.3s;

  display: flex;

  .form__field-wrapper {
    @media (min-width: $stop-scaling) {
      width: calc(100% - 310px);
    }

    @media (orientation: portrait) {
      width: calc(100% - 5rem);
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: calc(100% - 9rem);
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: calc(100% - 5rem);
    }
  }

  .form__field {
    width: 6rem;
    opacity: 0;
    animation: $inputAnimationDuration ease-in $buttonAnimationDelay 1 normal forwards formFieldAppearance2;

    @media (orientation: portrait) {
      width: 4rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 4rem;
    }

    @keyframes formFieldAppearance2 {
      0% {
        padding: 0;
        opacity: 0;
      }

      10% {
        opacity: 1;
      }

      100% {
        width: 100%;
        opacity: 1;
      }
    }
  }

  .form__field-placeholder {
    position: absolute;
    top: 50%;
    left: 5.6rem;
    z-index: 1;
    font: inherit;
    font-size: 2.4rem;
    color: rgb(117, 117, 117);
    text-transform: uppercase;
    transform: translateY(-50%);
    animation: 0.3s linear ($buttonAnimationDelay + $inputAnimationDuration) 1 normal both fadeIn;
    pointer-events: none;
  }

  .form__button {
    animation: $buttonAnimationDuration linear $buttonAnimationDelay 1 normal both fadeIn;
  }

  .form__button-text {
    animation: 0.3s linear $buttonAnimationDelay + $buttonAnimationDuration 1 normal both fadeIn;
  }
}
